*{
  margin: 0px;
  padding: 0px;
}
.App{
 display:block;
margin: 0px auto;
}
body{
  background:url('/src/img/cancha.jpg') ;
  background-size: cover;
  /* fallback for old browsers */
  background-attachment: fixed;
}

.leagues-container{
  display: grid;
 justify-content: center;
  width: 70%;
  gap:1%;
  margin:0px auto;
  margin-top: 1%;
  grid-template-columns: repeat(6,1fr);
  grid-template-rows: auto;
  
}
.leagues-container .leagues{
  background-color:RGB(255,255,255);
  
  color:rgb(8, 5, 5);
  font-family:  'Arial Narrow', Arial, sans-serif; 
  font-size: 8px;
  width: 100%;
 margin-bottom: 2%;
  text-align: center;
  border: 1px solid rgb(90, 89, 89);
  border-radius: 5px;
 }
 .leagues-container .leagues:hover{
  box-shadow: 1px 1px 18px 2px rgba(240,240,240,1);
 }

 
 .leagues-container .leagues img{
   border-radius: 3px;
   
}
 form{
   display:flex;
   width: 100%;
   height: 50px;
   justify-content: center;
   margin-top: 2%;
 }
 form .input-contain{
   width: 51%;
   height: 100%;
   background-color:rgb(8, 5, 5,0.5);
   display: flex;
   border-radius: 25px;
   border: 1px solid gray;
 }
 .input-contain .material-icons{
   font-size: 180%;
   line-height: 180%;
   margin-right: 10px;
   margin-left: 10px;
   color: gray;
 }
 .input-contain input{
   width: 80%;
   height: 100%;
   text-align: center;
   outline-style: none;
   color: white;
   background-color: transparent;
   border: none;
   font-size: 140%;
   
 }
.buttonInput{
   width: 10%;
   margin-left: 1%;
   height: 52px;
   line-height: 52px;
   border-radius: 25px;
   color: RGB(255,255,255);
   border: 1px solid rgb(41, 131, 206); ;
   background-color: rgb(50, 148, 228);
   font-size: 100%;
   font-weight: 400;
   font-family:Verdana, Geneva, Tahoma, sans-serif
 }
 .buttonInput:hover{
   background-color: RGB(255,255,255);
   color: rgb(48, 47, 47);
   border: none;
 }
 

@media(max-width:1350px){
  .leagues-container{
    width:80%
  }
}
@media(max-width:1169px){
  .leagues-container{
    width:90%
  }
  form{
    width: 100%;
    margin-top: 4%;
  }
  form .input-contain{
    width: 50%;
  }
}
@media(max-width:1041px){
  
  .leagues-container{
    width:90%
  }
  
  form .input-contain{
    width: 80%;
  }
  
}
@media(max-width:952px){
  .leagues-container img{
    width:60%;
  }
  form .input-contain{
    width: 70%;
  }
  .buttonInput{
    width: 10%;
  }
  
  
}
@media(max-width:768px){
 
  .leagues-container{
    gap:0.5%;
   
  }
  .leagues-container .leagues{
    margin-bottom: 0px;
  }
  .leagues-container img{
    width: 60%;
  }
  form{
    margin-top: 8%;
  }
  .buttonInput{
    width: 14%;
    font-size: 90%;
  }
}
@media(max-width:690px){
  .leagues-container{
    width: 95%;
  }
  .leagues-container img{
    width: 80%;
  }
  form{
    height: 40px;
  }
  form .input-contain{
    width: 60%;
  }
  .input-contain .material-icons{
    font-size: 150%;
    line-height: 180%;
  }
  .input-contain input{
   
    font-size: 110%;
    
  }
  .buttonInput{
    width: 20%;
    height: 42px;
    font-size: 80%;
    line-height: 42px;
  }
}
@media(max-width:500px){
  .input-contain input{
    text-align: start;
  }
  .buttonInput{
    width: 25%;
  }

}
@media(max-width:430px){
  form{
    height: 30px;
  }
  .input-contain .material-icons{
    font-size: 130%;
    line-height: 160%;
  }
  .input-contain input{
   font-size: 90%;
  }
  .buttonInput{
    width: 20%;
    height: 32px;
    font-size: 70%;
    line-height: 32px;
  }
}